.encabezados{
    text-align: center;
    width: 100%;
    padding: 20px 0;
    background: rgb(131, 131, 131);
    margin-bottom: 5px;
}
.home{
    text-align: center;
}
.tarjetas{
    border: 2px solid #000;
    border-radius: 10px;
    display: inline-block;
    margin: 0 10px;
    text-align: center;
    margin-bottom: 5px;
    width: 20%;
    overflow: hidden;
}
.tarjetas .img{
    position: relative;
    width: 100%;
    height: 210px;
}
.tarjetas .img .fondo{
    z-index: -1000;
    position: absolute;
    right: 0;
    -webkit-filter: blur(6px);
            filter: blur(6px);
    width: 100%;
    height: 210px;
}
.tarjetas .img .caratula{
    height: 200px;
    margin-top: 5px;
}
.tarjetas .video{
    width: 100%;
}
.tarjetas .tarjeta-body{
    padding: 10px;
    display: inline-block;
}
.tarjetas .tarjeta-body .titulo{
    height: 56px;
}
.tarjetas .tarjeta-body .sinopsis{
    width: 100%;
    text-align: justify;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.tarjetas .botones{
    width: 100%;
}
.tarjetas .botones .informacion{
    /* width: 49%; */
    width: 100%;
}
.tarjetas .botones .informacion a .boton{
    width: 100%;
    border-radius: 0 0 8px 8px;
    background: #026819;
    border: none;
    padding: 10px;
    color: #fff;
    font-size: 15.5px;
    font-weight: bold;
}
.tarjetas .botones .informacion a .boton:hover{
    -webkit-transition: 500ms;
    transition: 500ms;
    background: #00a526;
}
*{
    margin: 0;
    padding: 0;
}
body{
    font-family: sans-serif;
}
.header{
    width: 100%;
    background: #eee;
    display: flex;
    justify-content: center;
    align-items: center;
}
.header .logo{
    width: 10%;
    padding: 15px;
    display: inline-block;
}
.header .logo img{
    width: 100%;
}
.header .botones{
    display: inline-block;
    padding: 15px;
    width: 83%;
}
.header .botones ul{
    text-align: right;
    height: 100%;
    top: 0;
}
.header .botones ul li{
    list-style: none;
    display: inline;
    padding: 20px;
    text-align: center;
    margin: auto;
}
.header .botones ul li a{
    font-size: 35px;
    color: rgb(0, 0, 0);
    text-decoration: none;
    border-bottom: 2px solid transparent;
}
.header .botones ul li a:hover{
    border-bottom: 5px solid #000;
}
